import Scroll from "../../base/scroll/scroll.vue"
import Step from "../../base/step/step.vue"
import Upload from "../../base/upload/upload.vue"

export default {
  name: "getPrice1",
  components: {
    Step,
    Upload,
    Scroll
  },
  data() {
    return {
      stepArr: ["", ""],
      stepIndex: 0,
      isOk: true, //
      goodsImg: [],
      twoInfo: {},
      text: '',
      keyWord: ''
    }
  },
  //
  created() {
    let that = this;
    //
    this.$axios.post('/api/orderOperate/getAdditionalInfo', {
      buyerTaskRecordId: that.$route.query.buyerTaskRecordId
    }).then((res) => {
      let data = res.data
      if (data.code === "200") {
        this.twoInfo = data.data
      }
    }).catch((error) => {
      this.$vux.alert.show({
        title: '',
        content: '',
      })
    })
  },
  watch: {
    goodsImg: {
      handler(val) {
        if (val.length > 3) {
          this.$nextTick(() => {
            this.$refs.scroll.refresh()
          })
        }
      },
      deep: true
    }
  },
  //
  methods: {
    next() {
      if (this.text !== '' && this.goodsImg.length > 0) {
        let that = this;
        this.$axios.post('/api/orderOperate/backOrderSubmit', {
          buyerTaskRecordId: that.$route.query.buyerTaskRecordId,
          additionalFavorText: this.text,
          evaluateProductPicId: this.goodsImg
        }).then((res) => {
          console.log(res)
          if (res.data.code == 200) {
            this.$router.push({ name: 'submitSuccess', query: { type: 'evaluate4' } })
          }
        })
      } else {
        this.$vux.alert.show({
          title: '',
          content: '',
        })
      }
    }
  }
}



